.newPage1-page {
    display: flex;
    justify-content: flex-start;
    min-height: 600px;
    background-image: url('../../assets/home/background.png');
    background-size: cover;
    background-position: center;
    position: relative; /* Ensure proper stacking of elements */
}

.newPage1-content{

    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 40vw;
    
    margin-left: 80px;
    margin-top: 40px;
}


.newPage1-logo {
    align-self: self-start;
    width: 150px;
}

.newPage1-title {
    text-align: start;
    font-family: 'montserrat';
    color: white;
    font-size: 25px;
    margin-top: 10px;
    text-transform: uppercase;
    font-weight: bold;
    

}

.newPage1-border{
    align-self: flex-start;
    width: 35%;
    border: 2px white solid;
    margin-bottom: 5px;
    
}



.newPage1-sub-title{
    align-self: self-start;
    max-width: 85%;
    color: white;
    font-family: 'montserrat';
    /*font-family: 'assistant';*/
    font-size: 20px;
    
}

.newPage1-button{
    align-self: self-start;
    display: inline-block;
    padding: 10px 20px;
    background-color: #5e5e5e; 
    color: #fff; 
    font-family: 'assistant';
    font-size: 16px;
    text-transform: capitalize;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;

}

.newPage1-button:hover {
    background-color: #3a3a3a;
}

@media only screen and (max-width: 800px) {
    .newPage1-page{
        display: flex;
        
        min-height: 500px;
    }
    
    .newPage1-content{
        display: flex;
        
        max-width: 60%;
        margin-left: 10px;
        margin-top: 15px;
    }
    
    .newPage1-logo {
        
        width: 100px;
    }
    .newPage1-title {
        font-size: 14px;
        
    }

    .newPage1-sub-title{
        
        max-width: 100%;
        font-weight: bold;
        font-size: 13px;
        
    }
}
