
.page-fqa{
    display: flex;
    align-items: center;
    min-height: 100vh;
    background-color: #5e5e5e;
    flex-direction: column;
    
}

.div-fqa {
    margin-top: 10vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.div-fqa h3{
  color: gold;
}

.div-fqa h1{
    color:  white;
    margin-top: 10px;
    font-size: 70px;
}

.button-fqa {
  width: 500px;
  background-color: #d400ff;
  
  
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-bottom: 10px;
  font-size: 16px;
  transition: background-color 0.3s ease;
}


.button-fqa:hover {
  background-color: #800080;
}

.button-clicked {
    background-color: #800080;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-bottom: 10px;
    font-size: 16px;
    
  }

  .div-answer {
    width: 550px;
    height: 150px;
    background-color: white;
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  .answer {
    font-family: 'lato light';
    font-weight: bold;
    max-width: 95%;
    font-size: 15px;
    
    
    
  }

  .button-answer {
    background-color: #f3f3f3ad;
    color: rgba(14, 90, 177, 0.808);
    border: none;
    padding: 8px 16px;
    font-size: 14px;
    font-weight: bold;
    cursor: pointer;
    border-radius: 20px;
    outline: none;
    transition: background-color 0.3s ease;
    display: inline-flex;
    align-items: center;
  }
  
  .button-answer:hover {
    background-color: #ddd;
  }

  @media only screen and (max-width: 800px) {

    .page-fqa{
      min-height: 400px;
    }

    .div-fqa {
      
      margin-top: 5px;
      text-align: center;
    }
    .div-fqa h3{
      font-size: 20px;
    }
    .div-fqa h1{
      font-size: 35px;
    }


    .button-fqa{
      padding: 8px 10px;
      font-size: 10px;
      max-width: 60vw;
    }
    .div-answer{
      
      max-width: 70vw;
      width: 550px;
      height: 100px;
    }

    .answer{
      font-size: 10px;
    }
    .button-answer {
      font-size: 10px;
    }

  }